.addresses{

    .container{
        position: relative;
    }

    .addA{
        position: absolute;
        z-index: 10;
        top: 0px;
        right: 10px;
        color: $secondary;
        font-size: 12px;

        i{
            text-align: center;
            height: 18px;
            width: 18px;
            line-height: 17px;
            background: #fff;
            border: 1px solid $secondary;
            font-size: 13px;
            @include border-radius(50%);
            margin-right: 4px;
        }
    }

    .addressList{
        margin-top: 15px;
        overflow-x: hidden;
        overflow-y: auto;
        max-height: 180px;

        &::-webkit-scrollbar{
            width: 0;
        }

        .card-address{

            & + .card-address{
                margin-top: 8px;
            }

            .addE{
                position: absolute;
                z-index: 10;
                top: 0px;
                right: 2px;
                color: $secondary;
                font-size: 14px;
                padding: 8px;
            }

            flex-flow: row;
            padding: 10px;

            .address{
                padding-right: 20px;

                h4{
                    font-size: 14px;
                    margin-bottom: 5px;
                    font-weight: 700;

                    .badge{
                        font-size: 12px;
                        border: 1px solid #e1e1e1;
                        @include border-radius(2px);
                    }
                }

                p{
                    font-size: 13px;
                    margin: 0;
                }
            }
        }
    }
}


// Upload
.uploadSec{

    .btns{
        margin-top: 15px;
        
        .btn{
            font-size: 12px;
            padding: 5px 10px;
        }
    }
}

// Modal
.selIssues{

    .issues{
        display: flex;
        flex-flow: row wrap;
        align-items: center;
        justify-content: center;
        margin-bottom: 20px;

        .badge{
            flex-grow: 1;
            width: auto;
            border: 1px solid $primary;
            color: $primary;
            background: #fff;
            font-size: 14px;
            @include border-radius(20px);
            margin: 3px;
            padding: 8px 10px;

            &.active, &:focus{
                background: $primary;
                color: #fff;
                border-color: darken($primary, 10%);
                outline: none;
            }
        }
    }

    .form-group{

        .form-control{
            background: #f7f7f7;
            border: 1px solid #e1e1e1;
        }

        .hint{
            text-align: right;
            font-size: 12px;
            display: block;
        }
    }
}

.uploadArea{
    margin-top: 15px;
    // border: 1px dashed #e1e1e1;
    // background: #f7f7f7;
    // padding: 20px 5px 5px;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.uploadOpts{
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;

    @include media-breakpoint-up(lg){
        display: none;
    }

    a{
        margin: 6px;
        text-align: center;
        font-size: 13px;
        color: $gray-800;

        @include media-breakpoint-up(md){
            font-size: 15px;
        }

        i{
            display: block;
            margin: 0 auto 2px;
            height: 50px;
            width: 50px;
            line-height: 48px;
            text-align: center;
            font-size: 26px;
            background: $gray-100;
            border: 1px solid #e1e1e1;
            @include border-radius(50%);
        }

        &:hover{
            color: $primary;
        }
    }
}

.uploadOptsWeb{
    display: none;
    text-align: left;

    @include media-breakpoint-up(lg){
        display: block;
    }
}

.card-issues{
    padding: 10px;
    margin-top: 10px;

    h4{
        color: $primary;
        font-size: 14px;
        margin-bottom: 8px;
        font-weight: 500;
        text-transform: uppercase;

        @include media-breakpoint-up(md){
            font-size: 16px;
        }
    }

    .issuesSelected{
        li{
            padding-right: 40px;
            position: relative;
            font-size: 14px;
            line-height: 24px;

            > i{
                color: $gray-800;
                margin-right: 2px;
                width: 20px;
            }

            > a{
                position: absolute;
                right: 0px;
                top: 0px;
                font-size: 16px;
                padding: 0 5px;
                color: $secondary;
            }

            &:hover{
                background: #f7f7f7;
            }
        }

        @include media-breakpoint-up(md){
            display: flex;

            li{

                &:not(:last-child){
                    margin-right: 29px;
                }
                
            }
        }
    }

    .form-group{

        label{
            padding-left: 0;
        }

        .form-control-plaintext{
            font-size: 14px;
            padding-top: 0;
            padding-right: 30px;
        }

        .mEdit{
            position: absolute;
            z-index: 10;
            top: 0px;
            right: 0px;
            color: $secondary;
            font-size: 14px;
            padding: 0 8px;
        }
    }

    .btns{
        margin-top: 0;
        text-align: left;
        .btn{
            display: inline-block;
            width: auto;
            padding: 3px 10px;
        }
    }
}

// PMS
.pmsSec{

    .form-group{
        display: flex;
        align-items: center;
        padding: 20px 0;
        margin: 0;

        .form-control{
            background: #F5F6F8;
            color: #000;
            border: 1px solid #e1e1e1;
            @include border-radius(6px);

        }

        .pmsInfoBtn{
            font-size: 20px;
            color: $primary;
            margin-left: 10px;
        }
    }
}